@media(max-width: 991px) {
    :root {
        --fz: 16px !important;
    }
    .container {
        padding: 0 100px 0 50px;
    }
    .header{
        &-container {
            padding: 0 100px 0 50px;
        }
        .aside-btn{
            height: 60px;
        }
    }
    .section-1{
        .intro-slider{
            &__descr{
                margin-bottom: 60px;
            }
            &-link{
                p{
                    font-size: 14px;
                }
            }
            &-item{
                margin-right: 20px !important;
            }
        }
        &-container {
            margin: 0 auto;
            padding-left: 50px;
        }
    }
    .section-5{
        .intro-slider{
            &__descr{
                margin-bottom: 60px;
            }
        }
        &-container {
            margin: 0 auto;
            padding-left: 50px;
            
        }
    }
    .section-2{

        &__content-1 {
            padding-left: 50px;
            padding-right: 90px;
            width: 100%;
            &__title {
                font-size: 24px !important;
                margin-bottom: 20px !important;
            }
            &__descr{
                font-size: 14px !important;
            }
            &__descr:not(:last-child){
                margin-bottom: 10px;
            }
        }
    }
    .section-3{
        &-title,
        &__items,
        &-wrapper
        &__title{
            margin-bottom: 15px;
        }
        &-wrapper {
            margin-bottom: 20px;
        }
    }
    .section-4{
        &__wrapper {
            &-left{
                padding:0 90px 0 50px;
            }
        }
    }
    .section-5 {
        &-content {
            margin-bottom: 40px;
        }
        .intro-slider{
            &__title {
                margin-bottom: 30px;
            }
            &__descr:not(:last-child){
                margin-bottom: 15px;
            }
            &__descr:last-child{
                margin-bottom: 0;
            }
        }
    }
    .section-6{
        &-container {
            padding: 40px 100px 10px 50px !important;
        }
        .footer__copyright{
            padding-top: 15px;
            padding-bottom: 15px;
            padding-left: 50px;
        }
    }
    
}